import RPlayer from 'rplayer';
import { LinerAdsItem, AdsOpts, processOpts } from './options';

export default class Ads {
  private player: RPlayer;
  private readonly opts: AdsOpts;
  private readonly linerAtItems: LinerAdsItem[] = [];
  private readonly linerWaitItems: LinerAdsItem[] = [];
  private prevPlayCurrentTime = 0;

  constructor(opts: AdsOpts) {
    this.opts = processOpts(opts);
  }

  init(): void {
    this.opts.liner.forEach((ad) => {
      ad.playAt = ad.playAt || 0;
      if (ad.playWait) {
        this.linerWaitItems.push(ad);
      } else {
        this.linerAtItems.push(ad);
      }
    });
    this.linerAtItems.sort((a, b) => a.playAt - b.playAt);
    this.linerWaitItems.sort((a, b) => a.playWait - b.playWait);
  }

  install(player: RPlayer): void {
    this.player = player;
  }

  disablePlayer(): void {
    this.prevPlayCurrentTime = this.player.currentTime;
    this.player.on(RPlayer.Events.PLAY, this.pausePlayer);
    this.player.on(RPlayer.Events.TIME_UPDATE, this.pausePlayer);
    this.player.pause();
  }

  restorePlayer(): void {
    this.player.off(RPlayer.Events.PLAY, this.pausePlayer);
    this.player.off(RPlayer.Events.TIME_UPDATE, this.pausePlayer);
    this.player.currentTime = this.prevPlayCurrentTime;
    this.player.play();
  }

  private pausePlayer = (): void => {
    this.player.pause();
  };
}
